/* CARD SET */
.card-set {
  padding: 0;
  margin: 0;
  list-style: none;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;

  @media screen and (min-width: 320px) {
    margin-top: 20px;
  }

  @media screen and (min-width: 768px) {
    margin-left: calc(-1 * var(--card-set-gap-top));
  }

  @media screen and (min-width: 1024px) {
    margin-left: calc(-1 * var(--card-set-gap));
  }
}

.card-heading {
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  line-height: calc(16 / 12);
  color: #000000;
  text-transform: uppercase;
}

.card-set>.item {
  margin-top: 20px;
  transition: all .25s cubic-bezier(.34, .04, .37, .73);
  cursor: pointer;

  @media screen and (min-width: 768px) {
    width: calc((100% - 2 * var(--card-set-gap-top)) / 2);
    margin-left: var(--card-set-gap-top);
    margin-top: var(--card-set-gap-top);

  }

  @media screen and (min-width: 1024px) {
    width: calc((100% - 3 * var(--card-set-gap)) /3);
    margin-top: var(--card-set-gap-top);
    margin-left: var(--card-set-gap);

  }
}

/* CARD */
.card {
  box-shadow: var(--card-shadow);
  border-radius: 3px;
  transition: box-shadow 350ms cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
}

.card:hover,
.card:focus {
  box-shadow: 4px 4px 4px 4px rgba(1, 75, 48, 0.541);
  border-radius: 3px 3px 3px 3px;
}

.card-content {
  padding-top: 8px;
}

.card-text {
  /* margin-top: 10px; */
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  line-height: calc(16 / 12);
  color: #FF6B08;
}